/** @augments {Component<{icon: string}, {}>} */
class Icon extends Component {
    static propTypes = {
        icon: PropTypes.oneOf([
            "reset", "play", "step", "pause",
            "import", "export",
            "help", "github", "alert", "load",
            "close",
        ]).isRequired,
    }

    render() {
        return (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
            >
                { icons[this.props.icon] || undefined }
            </svg>
        );
    }
}

export default Icon;



